import { ToastContainer, toast } from 'react-toastify';

export default function toastify() {
     toast("Wow so easy!",{
        position: "top-right",
        autoClose: 5000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
     });
}
