import React, { useEffect, useRef, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux';
import { useSpring,animated } from 'react-spring';
import { thunkfetchMyHistory } from '../../reduxStore/CartSlice';

export default function HistoryCart() {
  const DrawerRight = useRef();

  const [showDrawer, setShowDrawer] = useState(false);
  const [array, setArray] = useState([]);
  const buyerHistory = useSelector(state => state.cart.buyerHistory);
  const dispatch = useDispatch()
  useEffect(()=>{
    dispatch(thunkfetchMyHistory())
  },[])
  useEffect(() => {
    console.log(buyerHistory);
    setArray(buyerHistory)
}, [buyerHistory])
  const style = useSpring({
    to: async animate => {
      await animate({ to: { right: showDrawer ? 0 : -300, opacity: showDrawer ? 1 : 0, }, config: { duration: 500 } });
      const handleClickOutside = e => {
        if (!DrawerRight?.current?.contains?.(e.target)) { setShowDrawer(false); }
      };
      showDrawer ? document.addEventListener('mousedown', handleClickOutside) : document.removeEventListener('mousedown', handleClickOutside);
    }
  })



function HistoryItem(props) {
  const { info } = props
  
  return (<div className='w-full h-[100px] flex justify-start items-center border-b-[1px] border-slate-100 p-2' >
            <div className='h-full w-[100px] bg-slate-200 p-2'>
                {/* <div className='h-full aspect-square bg-cover' style={{backgroundImage:`url(${info?.ItemDetail?.Product?.productImage})`}}> */}
                {/* </div> */}
            </div>
            <div>{info.date}</div>
        </div>)
}

  return (
    <div ref={DrawerRight}>

      <i  className="fa-solid fa-clock-rotate-left fa-2xl m-5 cursor-pointer" onClick={e => setShowDrawer(true)} />

      <animated.div className={`fixed top-0 right-0 h-min-screen w-[20vw] bg-white rounded-l-3xl z-[40]  shadow-2xl`} style={style} >
        <h1 className='w-full h-fit flex justify-center items-center p-5 border-b-gray-200 border '>HISTORY</h1>
        <div className='flex flex-col h-full items-center  gap-2 overflow-y-scroll'>
          {array?.map((i,d) => <HistoryItem key={d} info={i} />)}
         </div>
      </animated.div>

    </div>
  )
}
