import { createSlice } from "@reduxjs/toolkit";
import { addCartApi, buyCartApi, deleteCartApi, fetchMyCartApi, fetchUserHistoryApi } from "../api/cartApi";
import { loading } from "./LoadingSlice";

const CartSlice = createSlice({
    name: 'cart',
    initialState: { Mycart:[],buyerHistory:[]},
    reducers: {
        addCart: (state, action) => {
            console.log(action.payload,"1");
            state.Mycart = [...action.payload]
        },
        fetchCart: (state, action) => {
            console.log(action.payload,"2");
            state.Mycart = [...action.payload]
        },
        deleteCart:(state,action) => {
            state.Mycart = []

        },
        addHistory:(state,action) => {
            state.buyerHistory = [...state.buyerHistory,action.payload]

        },
        fetchHistory:(state,action) => {
            state.buyerHistory = action.payload

        },
    }
})


export const thunkAddCart = (input) => async dispatch => {
    try {
        dispatch(loading(true))
        const res = await addCartApi(input);
        dispatch(fetchCart(res.data));

    } catch (error) {
        throw error
    }
    finally{
        dispatch(loading(false))
    }
}
export const thunkfetchMyCart = (input) => async dispatch => {
    try {
        dispatch(loading(true))
        const res = await fetchMyCartApi(input);
        dispatch(fetchCart(res.data));

    } catch (error) {
        throw error
    }
    finally{
        dispatch(loading(false))
    }
}
export const thunkDeleteCart = (id) => async dispatch => {
    try {
        dispatch(loading(true))
        const res = await deleteCartApi(id);
        dispatch(deleteCart([]));

    } catch (error) {
        throw error
    }
    finally{
        dispatch(loading(false))
    }
}
export const thunkBuyCart = () => async dispatch => {
    try {
        dispatch(loading(true))
        const res = await buyCartApi();
        console.log('add',res.data);
        dispatch(addHistory(res.data));
        dispatch(deleteCart([]));

    } catch (error) {
        throw error
    }
    finally{
        dispatch(loading(false))
    }
}
export const thunkfetchMyHistory = () => async dispatch => {
    try {
        dispatch(loading(true))
        const res = await fetchUserHistoryApi();
        console.log('fetch',res.data);

        dispatch(fetchHistory(res.data));

    } catch (error) {
        throw error
    }
    finally{
        dispatch(loading(false))
    }
}
export default CartSlice.reducer
const { addCart,fetchCart,deleteCart,addHistory,fetchHistory} = CartSlice.actions;
export { addCart,fetchCart,deleteCart,addHistory,fetchHistory}